<template>
  <div class="part">
    <ARow>
      <ACol v-bind:xs="16" v-bind:sm="6" v-bind:md="6">
        <div class="part-title-wrap">
          <h3 class="part-title">{{type}}</h3>
          <ASwitch :checked="active" v-on:change="toggle" />
        </div>
      </ACol>
      <ACol v-bind:xs="24" v-bind:sm="18" v-bind:md="18">
        <ARow v-bind:gutter="12">
          <ACol
            :xs="24"
            :sm="12"
            :md="8"
            :lg="6"
            v-for="{best, group, link, name, price, selector} of listings"
          >
            <Listing
              :best="best.toFixed(2)"
              :group="group"
              :key="selector"
              :name="name"
              :price="price.toFixed(2)"
              :link="link"
              :selector="selector"
              :selected="selected"
              @select="setSelected"
              @priceChange="setPrice"
            />
          </ACol>
        </ARow>
      </ACol>
    </ARow>
  </div>
</template>

<script>
import Listing from "./Listing";

export default {
  components: { Listing },
  data: function() {
    return {
      selected: (
        this.listings.find(l => l.selected === true) || this.listings[0]
      ).selector
    };
  },
  methods: {
    toggle: function(checked) {
      this.$emit("toggle", this.type, checked);
    },
    setSelected: function(selector) {
      this.selected = selector;
      const listing = this.listings.find(l => l.selector === selector);
      this.$emit("change", this.type, selector, listing.name);
    },
    setPrice: function(selector, price) {
      const listing = this.listings.find(l => l.selector === selector);
      listing.price = parseFloat(price);
      if (listing.best > listing.price) listing.best = listing.price;
      this.$emit(
        "changePrice",
        this.type,
        listing.name,
        listing.price,
        listing.best
      );
    }
  },
  name: "PartType",
  props: {
    active: Boolean,
    icon: String,
    listings: Array,
    optional: Boolean,
    type: String
  }
};
</script>

