.part {
  border-bottom: solid 1px #f5f5f5;
  justify-content: center;
  margin-bottom: 10px;
  padding-bottom: 5px;
}
.part-title-wrap {
  display: flex;
  flex-grow: 1;
  justify-content: flex-end;
  margin-bottom: 8px;
  padding: 0 10px 0 0;
  text-align: left;
}
.part-title {
  margin: 0 15px 0 0;
}
</style>
